'use client'

// components/Contacto.jsx - VERSI√ìN PROFESIONAL
import { Mail, Phone, MapPin, Facebook, MessageCircle, Clock, Send, Instagram } from 'lucide-react'
import { useInView } from '@/hooks/useInView'
import { useState } from 'react'
import { API_ENDPOINTS } from '@/lib/config'

export default function Contacto() {
  const [ref, isVisible] = useInView()
  const [loading, setLoading] = useState(false)
  const [message, setMessage] = useState('')

  const handleSubmit = async (e) => {
    e.preventDefault()
    setLoading(true)
    setMessage('')

    // Sanitizar y validar datos del formulario
    const nombre = e.target.nombre.value.trim()
    const telefono = e.target.telefono.value.trim()
    const email = e.target.email.value.trim().toLowerCase()
    const servicio = e.target.servicio.value.trim()
    const fecha = e.target.fecha.value
    const mensaje = e.target.mensaje.value.trim()

    // Validaci√≥n b√°sica del lado del cliente
    if (!nombre || nombre.length < 2) {
      setMessage('Por favor ingresa un nombre v√°lido (m√≠nimo 2 caracteres)')
      setLoading(false)
      return
    }

    if (!email || !/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email)) {
      setMessage('Por favor ingresa un email v√°lido')
      setLoading(false)
      return
    }

    if (!telefono || telefono.length < 8) {
      setMessage('Por favor ingresa un tel√©fono v√°lido')
      setLoading(false)
      return
    }

    const formData = {
      nombre,
      telefono,
      email,
      servicio,
      fecha_evento: fecha || null,
      mensaje
    }

    try {
      const response = await fetch(API_ENDPOINTS.CONTACTO, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(formData)
      })

      // Verificar si la respuesta es JSON
      let data
      const contentType = response.headers.get('content-type')
      if (contentType && contentType.includes('application/json')) {
        data = await response.json()
      } else {
        const text = await response.text()
        setMessage(`Error del servidor: ${text || 'Respuesta inv√°lida'}`)
        setLoading(false)
        return
      }

      if (data.success) {
        setMessage('¬°Solicitud enviada exitosamente! Te contactaremos pronto.')
        e.target.reset()
      } else {
        setMessage(data.message || 'Error al enviar. Intenta de nuevo.')
      }
    } catch (error) {
      console.error('Error en contacto:', error)
      setMessage('Error al conectar con el servidor. Por favor intenta m√°s tarde.')
    } finally {
      setLoading(false)
    }
  }
  return (
    <section id="contacto" ref={ref} className="py-24 bg-gradient-to-b from-white to-pink-50 relative overflow-hidden">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 relative z-10">
        {/* Header */}
        <div className={`text-center mb-20 will-animate ${isVisible ? 'animate-in-up' : ''}`}>
          <span className="text-pink-600 font-bold text-lg uppercase tracking-wider">Hablemos</span>
          <h2 className="text-5xl md:text-6xl font-bold text-gray-800 mb-6">
            ¬°Hagamos Realidad tu Evento!
          </h2>
          <p className="text-xl text-gray-600 max-w-3xl mx-auto leading-relaxed">
            Estamos listos para hacer realidad tus ideas. Respuesta en menos de 24 horas.
          </p>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-12">
          {/* Left Side - Contact Info */}
          <div className={`space-y-6 will-animate ${isVisible ? 'animate-in-left' : ''}`}>
            {/* Main Contact Card */}
            <div className="bg-gradient-to-br from-pink-500 to-orange-500 p-8 rounded-2xl shadow-xl text-white">
              <h3 className="text-2xl font-bold mb-6">Contacto</h3>

              <div className="space-y-6">
                <div className="flex items-start gap-4">
                  <div className="w-12 h-12 bg-white/20 rounded-lg flex items-center justify-center flex-shrink-0">
                    <Phone className="w-6 h-6 text-white" />
                  </div>
                  <div>
                    <p className="font-bold mb-1">Tel√©fono / WhatsApp</p>
                    <a href="tel:+56977794373" className="text-xl font-bold hover:underline">
                      +569 7779 4373
                    </a>
                  </div>
                </div>

                <div className="group flex items-start gap-6">
                  <div className="w-16 h-16 bg-white/20 backdrop-blur-sm rounded-2xl flex items-center justify-center flex-shrink-0 group-hover:scale-110 group-hover:rotate-12 transition-all duration-300">
                    <Mail className="w-8 h-8 text-white" />
                  </div>
                  <div>
                    <p className="font-bold text-lg mb-2 text-white/90">Email</p>
                    <a href="mailto:ventas@sabrotortas.cl" className="text-2xl font-bold hover:underline">
                      ventas@sabrotortas.cl
                    </a>
                    <p className="text-white/80 text-sm mt-1">Respuesta en 24hrs</p>
                  </div>
                </div>

                <div className="group flex items-start gap-6">
                  <div className="w-16 h-16 bg-white/20 backdrop-blur-sm rounded-2xl flex items-center justify-center flex-shrink-0 group-hover:scale-110 group-hover:rotate-12 transition-all duration-300">
                    <MapPin className="w-8 h-8 text-white" />
                  </div>
                  <div>
                    <p className="font-bold text-lg mb-2 text-white/90">Ubicaci√≥n</p>
                    <p className="text-xl font-bold">Santiago, Chile</p>
                    <p className="text-white/80 text-sm mt-1">Cobertura en toda la RM</p>
                  </div>
                </div>
              </div>

              {/* Social Media */}
              <div className="mt-10 pt-10 border-t border-white/30">
                <p className="font-bold text-lg mb-6 text-white/90">S√≠guenos en Redes Sociales</p>
                <div className="flex gap-4">
                  <a
                    href="https://www.instagram.com/sabrotortas.cl/"
                    target="_blank"
                    rel="noopener noreferrer"
                    className="group/social w-14 h-14 bg-white/20 backdrop-blur-sm hover:bg-white rounded-2xl flex items-center justify-center transition-all duration-300 hover:scale-110 hover:rotate-12"
                  >
                    <Instagram className="w-7 h-7 text-white group-hover/social:text-pink-500 transition-colors" />
                  </a>
                  <a
                    href="https://wa.me/56977794373"
                    target="_blank"
                    rel="noopener noreferrer"
                    className="group/social w-14 h-14 bg-white/20 backdrop-blur-sm hover:bg-white rounded-2xl flex items-center justify-center transition-all duration-300 hover:scale-110 hover:rotate-12"
                  >
                    <MessageCircle className="w-7 h-7 text-white group-hover/social:text-green-500 transition-colors" />
                  </a>
                </div>
              </div>
            </div>

            {/* Schedule Card */}
            <div className="bg-white p-8 rounded-3xl shadow-xl border-2 border-gray-100">
              <div className="flex items-center gap-4 mb-6">
                <div className="w-14 h-14 bg-gradient-to-br from-purple-100 to-pink-100 rounded-2xl flex items-center justify-center">
                  <Clock className="w-7 h-7 text-purple-600" />
                </div>
                <h4 className="font-bold text-2xl text-gray-800">Horario de Atenci√≥n</h4>
              </div>
              <div className="space-y-4">
                <div className="flex justify-between items-center pb-4 border-b border-gray-200">
                  <span className="text-gray-700 font-semibold">Lunes a Viernes</span>
                  <span className="text-pink-600 font-bold">9:00 - 19:00</span>
                </div>
                <div className="flex justify-between items-center pb-4 border-b border-gray-200">
                  <span className="text-gray-700 font-semibold">S√°bados</span>
                  <span className="text-pink-600 font-bold">10:00 - 14:00</span>
                </div>
                <div className="flex justify-between items-center">
                  <span className="text-gray-700 font-semibold">WhatsApp</span>
                  <span className="text-green-600 font-bold">24/7</span>
                </div>
              </div>
            </div>
          </div>

          {/* Right Side - Contact Form */}
          <div className="bg-white p-10 rounded-3xl shadow-2xl border-2 border-gray-100">
            <div className="mb-8">
              <h3 className="text-3xl font-bold text-gray-800 mb-3">Solicita tu Cotizaci√≥n</h3>
              <p className="text-gray-600">Completa el formulario y te contactaremos en menos de 24 horas</p>
            </div>

            <form onSubmit={handleSubmit} className="space-y-6">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div>
                  <label htmlFor="nombre" className="block text-sm font-bold text-gray-700 mb-3">
                    Nombre Completo *
                  </label>
                  <input
                    type="text"
                    id="nombre"
                    name="nombre"
                    required
                    className="w-full px-5 py-4 rounded-2xl border-2 border-gray-200 focus:ring-2 focus:ring-pink-500 focus:border-transparent outline-none transition-all text-gray-800 font-medium"
                    placeholder="Tu nombre"
                  />
                </div>

                <div>
                  <label htmlFor="telefono" className="block text-sm font-bold text-gray-700 mb-3">
                    Tel√©fono *
                  </label>
                  <input
                    type="tel"
                    id="telefono"
                    name="telefono"
                    required
                    className="w-full px-5 py-4 rounded-2xl border-2 border-gray-200 focus:ring-2 focus:ring-pink-500 focus:border-transparent outline-none transition-all text-gray-800 font-medium"
                    placeholder="+56 9 xxxx xxxx"
                  />
                </div>
              </div>

              <div>
                <label htmlFor="email" className="block text-sm font-bold text-gray-700 mb-3">
                  Email *
                </label>
                <input
                  type="email"
                  id="email"
                  name="email"
                  required
                  className="w-full px-5 py-4 rounded-2xl border-2 border-gray-200 focus:ring-2 focus:ring-pink-500 focus:border-transparent outline-none transition-all text-gray-800 font-medium"
                  placeholder="tu@email.com"
                />
              </div>

              <div>
                <label htmlFor="servicio" className="block text-sm font-bold text-gray-700 mb-3">
                  Servicio de Inter√©s *
                </label>
                <select
                  id="servicio"
                  name="servicio"
                  required
                  className="w-full px-5 py-4 rounded-2xl border-2 border-gray-200 focus:ring-2 focus:ring-pink-500 focus:border-transparent outline-none transition-all text-gray-800 font-medium"
                >
                  <option value="">Selecciona un servicio</option>
                  <option value="Servicios de Catering">Servicios de Catering</option>
                  <option value="Tortas Decoradas">Tortas Decoradas</option>
                  <option value="Preparaci√≥n de Eventos">Preparaci√≥n de Eventos (Contamos con el personal y el equipamiento necesario para la preparaci√≥n de presentaciones y celebraciones familiares y de empresas. Entregamos todo lo necesario para la preparaci√≥n de eventos desde el amoblado (sillas y mesas) hasta la decoraci√≥n y ambientaci√≥n de locales.)</option>
                </select>
              </div>

              <div>
                <label htmlFor="fecha" className="block text-sm font-bold text-gray-700 mb-3">
                  Fecha del Evento (aproximada)
                </label>
                <input
                  type="date"
                  id="fecha"
                  name="fecha"
                  className="w-full px-5 py-4 rounded-2xl border-2 border-gray-200 focus:ring-2 focus:ring-pink-500 focus:border-transparent outline-none transition-all text-gray-800 font-medium"
                />
              </div>

              <div>
                <label htmlFor="mensaje" className="block text-sm font-bold text-gray-700 mb-3">
                  Cu√©ntanos sobre tu evento *
                </label>
                <textarea
                  id="mensaje"
                  name="mensaje"
                  rows="5"
                  required
                  className="w-full px-5 py-4 rounded-2xl border-2 border-gray-200 focus:ring-2 focus:ring-pink-500 focus:border-transparent outline-none transition-all resize-none text-gray-800 font-medium"
                  placeholder="Describe tu evento, n√∫mero de personas, preferencias especiales..."
                ></textarea>
              </div>

              {message && (
                <div className={`p-4 rounded-2xl text-center font-semibold ${message.includes('exitosamente') ? 'bg-green-100 text-green-800' : 'bg-red-100 text-red-800'}`}>
                  {message}
                </div>
              )}

              <button
                type="submit"
                disabled={loading}
                className="group w-full bg-gradient-to-r from-pink-500 via-orange-500 to-pink-500 bg-size-200 bg-pos-0 hover:bg-pos-100 text-white py-5 rounded-2xl font-bold text-lg shadow-xl hover:shadow-2xl transform hover:scale-105 transition-all duration-300 flex items-center justify-center gap-3 disabled:opacity-50 disabled:cursor-not-allowed"
              >
                {loading ? 'Enviando...' : 'Enviar Solicitud'}
                {!loading && <Send className="w-6 h-6 group-hover:translate-x-2 transition-transform" />}
              </button>

              <p className="text-sm text-gray-500 text-center">
                * Campos obligatorios - Responderemos en menos de 24 horas
              </p>
            </form>
          </div>
        </div>
      </div>

      <style jsx>{`
        .bg-size-200 {
          background-size: 200% 200%;
        }
        .bg-pos-0 {
          background-position: 0% 50%;
        }
        .bg-pos-100 {
          background-position: 100% 50%;
        }
      `}</style>
    </section>
  )
}